<template>
   <div>
      <section class="bg-white">
         <h2 class="text-3xl pt-6 text-blue 
         md:text-6xl
         lg:text-4xl">- Hotel -</h2>

         <span class="font-Clarissa text-5xl">Cuba</span>

         <!-- BUSCADOR -->
         <searcher v-on:updparams="updSearch"></searcher>
      </section>

      <section class="bg-white text-white py-6">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->
 
         <div class="bg-blue-180 pb-10">
            <!-- Listado polo -->
            <div class="flex flex-wrap justify-center gap-4 text-sm m-auto 
            md:text-2xl md:w-4/6">
            </div>

            <!-- Listado de productos por polo -->
            <div class="container flex flex-col gap-20 items-center py-10">
               <product-list-template
               v-for="(item, index) in product_list" :key="index"
               :item="item" :index="index" 
               :not_found="true"
               :params="{
                  poleKey: country,
                  productKey: product,
                  comb: search_params.rooms
               }"
               ></product-list-template>
               <span v-if="(this.product_list.length == 0)">No se encontro ningun precio para el rango de fecha determinado. Intente con otro rango.</span>    
            </div>
         </div>
      </section>
   </div>
</template>
<script>
import moment from 'moment'
import Searcher from "@/components/searcher/Searcher.vue";
import ProductListTemplate from '@/components/ProductListTemplate'

import Authorization from "@/services/user/auth.service";

export default {
   name: 'SearchResult',
   components: {
      Searcher,
      ProductListTemplate
   },
   data() {
      return {
         product: '',
         country: '',
         poles: [],
         search_params: {},         
         product_list: [            
         ],
         pole: ''
      }
   },   
   created() {           
      this.search()      
   },
   methods: {
      getPoleInfo(index) {
         // console.log(this.poles[index].name)
         this.poles.map((e, i) => {            
            e.active = (index == i) ? true : false
         })         
      },
      updSearch() {
         this.search()
      },
      search() {
         const params = JSON.parse(localStorage.getItem('searchform'))
         if(params) {            
            //Format Params      
            this.search_params = {
               hotel_id: params.hotel.value,	
               "date_from": moment(params.dateRange[0]).format('YYYY-MM-DD'),
               "date_to": moment(params.dateRange[1]).format('YYYY-MM-DD'),
               "pole_id": params.destination.value,
               "rooms": params.rooms
            }
         }
         // Searching by params
         this.search_params.auth = 'bearer'
         Authorization.checkLogin('/search/hotels/prices', this.search_params, 'post').then(e => {
            this.product = 'hotel',
            this.country = 'cuba'
            if(e) {                             
               this.product_list = e.data
               this.product_list.map(p => {                  
                  p.segment = {
                        name: "playa",
                        img: require("@/assets/img/segment/playa.png")
                     }
               })
               // console.log(this.product_list)
            }
         })
      }
   }
   
}
</script>
<style scoped>
   article {
      grid-template-rows: auto auto;
      grid-template-columns: auto;
   }

   @media screen and (min-width: 768px) {
      article {
         grid-template-columns: 27% auto;  
         grid-template-rows: auto;  
      }      
   }
</style>